import React from "react";
import 'tachyons';

const Card = ({robots}) => {
    const robolist = [];
    let cardlist = [];
    for (let i = 0; i < robots.length; i++){
        robolist.push({id: robots[i].id,name: robots[i].name,email: robots[i].email})
        cardlist.push(<div class="w5 grow center bg-white br3 pa3 pa4-ns mv3 ba b--black-10 shadow-3">
                        <div class="tc">
                            <img src= {`https://robohash.org/${robolist[i].id}? size=200x200`} class="br-100 h4 w4 dib ba b--black-05 pa2" title="robot" alt="robot"/>
                            <h1>{robolist[i].name}</h1>
                            <p>{robolist[i].email}</p>
                        </div>
                        </div>)
    }
    if (robots.length > 0 ){
        return(<div class='flex flex-wrap items-center'>
            {cardlist}
        </div>)
    }
    else{
    return(
        <div className="baskervile mv6">
             <p className='o-30 b 4rem'>Sorry we do not have the robot you're looking for</p>
        </div>
    )}
}

export default Card ;